import axios from '@/utils/fetch'

/**
 * 
 * @param {query} query
 */
export function getRoleTable (query) {
  return axios({
    url: '/selectRoleConfigure1',
    method: 'get',
    params: query
  })
}

/**
 * 
 * @param {id,name,despriction,createTime,upDateTime,createOper,updateOper} data
 */
export function addRole (data) {
  return axios({
    url: '/insertRole',
    method: 'post',
    data
  })
}

/**
 * 
 * @param {id} data
 */
export function delRole (data) {
  return axios({
    url: '/deleteRole/' + data,
    method: 'get'
  })
}
/**
 * ()
 * @param {id} data
 */
export function fetchAllRoleMesage (data) {
  return axios({
    url: '/selectRoleConfigure2',
    method: 'post',
    data
  })
}

/**
 * 
 * @param {id} data
 */
export function updateRole (data) {
  return axios({
    url: '/updateRole',
    method: 'post',
    data
  })
}

/**
 * 
 * @param {id} data
 */
export function fetchUser (id) {
  return axios({
    url: '/selectAllUser/' + id,
    method: 'post'
  })
}

/**
 * 
 * @param {id,sysUser} data
 */
export function removeRoleUser (id, data) {
  return axios({
    url: '/delectRoleUser/' + id,
    method: 'post',
    data
  })
}

/**
 * 
 * @param {*} query
 */
export function getRoleUser (id) {
  return axios({
    url: '/selectUserRole',
    method: 'post',
    id
  })
}

/**
 * 
 * @param {*} query
 */
export function addRoleUser (id, data) {
  return axios({
    url: '/insertRoleUser2/' + id,
    method: 'post',
    data
  })
}



